<template>
	<div>
		<div class="mask">
		</div>
		<div class="messageBox">
		<div class="heade">添加{{name.label}} </div>
		<div class="main">
			<span class="red delete_btn" type="text" size="small" @click="showDeleteSelect" v-if="showDelete">
					删除
			</span>
			<div class="deleteSelect" v-else><span class="blue"@click="confirmDelete">确定</span><span class="red"@click="cancelDelete">取消</span></div>
		<div class="add">
			<ul  class="tagGroup">
				<li class="tag" @click="selectTag(tag,index,$event.target)"  v-for="(tag,index) in addTags" :class="{selected:history==tag}">{{tag}}</li>
			</ul>
		</div>
			<el-form :model="$data" :rules="rules" >
			<el-form-item prop="inputRules">
			    <el-input v-model="inputValue"></el-input>
			<el-button @click="addTag">添加</el-button>
			</el-form-item>
			</el-form>
			<div class="btn">
				<el-button class="confirm_btn selected" @click="confirm">确定</el-button>
				<el-button @click="cancel">取消</el-button>
			</div>
		</div>
		</div>
	</div>
</template>

<script>
	import { apiMethods } from'../../api';
	
	import { ls } from "@/config/pageStore";
	export default{
		props:["name","history"],
		data() {
			return{
				addTags: [],
				historyTag:[],
		        inputValue: '',
		        value:"",
		        historyTagObj:{},
		        tagOj:"",
		        showDelete:true,
		        rules:{
			        inputRules:[
			        { required: true, message: '请输入标签名称，字数不超过4个字', trigger: 'blur' },
	            	{ min: 1, max: 4, message: '长度在 1到 4 个字符', trigger: 'blur' }]
			        }
			}
		},
		methods:{
		
			addTag() {
				
				for(var i in this.addTags){
					if(this.addTags[i]==this.inputValue){
						alert("你已经添加该项咯！")
						return;
					}else if(this.inputValue==""){
						alert("你还没有输入标签名！")
						return;
					}
				}
				this.addTags.push(this.inputValue);
				this.inputValue=""
				this.historyTag=this.addTags;
			},
			selectTag(tag,index) {
				$(".tag").eq(index).addClass("selected").siblings().removeClass("selected")
				this.value = tag;
			},
				//点击确定
			confirm() {
							
				if(this.value==""){
					this.value=this.history
				}
				let stringName = apiMethods.getBackstageName(this.name.id);
				var data = {save_label:1,
							label_name:stringName}
				data[stringName] = JSON.stringify(this.addTags);
				
				
				apiMethods.editTagArr(data,(resp)=>{
	      			 console.log(data)
	      })
	      			this.$emit('my-add',[this.value,this.name.id,this.addTags]);
	      			
			},
			cancel() {
				this.$emit('my-cancle',[this.name.id,this.addTags]);
			},
			//点击删除
			showDeleteSelect() {
				this.showDelete=false;
//				$(".tag").addClass("selecteDelete");
				
			},
			//确定删除
			confirmDelete() {
				this.showDelete=true
				
			},
			//取消删除
			cancelDelete() {
				this.showDelete=true
			}
		},
		created() {
			let getStringName = apiMethods.getBackstageName(this.name.id);
						apiMethods.getTagArr({
						get_shop_label:1,
						label_name:getStringName
					},(resp)=>{
						if(resp.data[getStringName]==undefined){
							this.addTags=ls.getItem("historyTagsObj");
						}else{
							this.addTags=resp.data[getStringName]
						}
			})

		}
	}
</script>

<style lang="scss" scoped>
@import 'src/styles/mixin.scss';
	.mask {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 180%;
		z-index: 9990;
		background: rgba(0,0,0,0.5);
	}
	.messageBox{
		width:540px ;
		height:350px;
		position: absolute;
		top: 408px;
		left: 690px;
		background: #FFFFFF;
		z-index: 9991;
	}
	.heade{
		font-family: MicrosoftYaHei;
		font-size: 16px;
		color: #FFFFFF;
		letter-spacing: 0.57px;
		text-align: center;
		background: #5A8CFF; 
		height: 40px;
		line-height: 40px;
	}
	.main{
		padding-left:80px;
		font-size: 14px;
	}
	.add{
		width: 100%;
		margin:40px 0 20px;
	}
	.el-input{
		width: 300px;
		height: 34px;
		margin-right:20px;
	}
	.el-button{
		width: 60px;
		height: 34px;
		color:#4877E7;
		border: 1px solid #4877E7;
	}
	.el-button:hover{
		color:#4877E7;
		border:1px solid #4877E7 ;
	}
	.btn .el-button{
		color: #4877E7;
		width: 160px;
		height: 40px;
		
	}
	.el-button.confirm_btn{
		background:#4877E7;
		color: #FFFFFF;
		margin-right: 60px;
	}
	.btn{
		margin-top: 20px;
	}
	.tag{
		padding: 5px 10px 5px;
		color: #4877E7;
		font-size: 13px;
		text-align: center;
		vertical-align: middle;
		margin:0 10px 10px;	
		border-radius: 5px;
		border: 1px solid #4877E7;
		display: inline-block;
	}
	.tagGroup{
		width: 400px;
		list-style: none;
	}
	.tag.selected{
		color: green;
		border: 1px solid green;
	}
	.red{
		font-size: 14px;
		color: #E7487E ;
	}
	.delete_btn{
		position: absolute;
		top: 50px;
		left:450px ;
	}
	.deleteSelect{
		position: absolute;
		top: 50px;
		left:440px ;
	}
	.blue{
		font-size: 14px;
		color:  #4877E7;
		margin-right: 10px;
	}
	.selecteDelete{
		border: 1px solid #E7487E;
		color:#E7487E ;
	}
	.dsn{
		display: none;
	}
</style>


